
html {
  font-size: 13.333vw;
}
body {
  margin: 0;
  font: 16px "Avenir", Helvetica, Arial, sans-serif;
}
body .app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body a {
  text-decoration: none;
  font-weight: normal;
  color: #333;
}
body ul {
  margin: 0;
  padding: 0;
}
body li {
  list-style: none;
  padding: 0;
  margin: 0;
}
body textarea {
  outline: none;
  border-radius: 8px;
}
body .button,
body button {
  line-height: 36px;
  height: 36px;
  color: #2572e5;
  padding: 6px 12px;
  background: transparent;
  outline: none;
  border: none;
  font-size: 16px;
}



